

















import Vue from 'vue';
import PokeCard from '@/components/PokeCard.vue';
import * as T from '@/@types';

export default Vue.extend({
  name: 'home',
  components: { PokeCard },
  created() {
    this.$store.dispatch('fetchData');
  },
  computed: {
    pokemonList(): T.PokemonList[] {
      return this.$store.getters.getPokemonList;
    },
  },
});
